import React from 'react';
// import logo from './logo.svg';
import './App.css';
import Table from './Table.js';
import WorkPage from './components/WorkPage.js';
import Footer from './Footer.js';

class App extends React.Component{

  constructor(props){
    super(props);
    this.state = {
      backColor : "App-footer",
      backFooter: "App"
    }
    this.handleWork = this.handleWork.bind(this);
    this.handleColor = this.handleColor.bind(this);
    console.log(this.state.backColor);
  
  }
  
  handleWork(){

    this.setState({

      work : true

    })
    console.log("The state has been set to true")

  }


  handleColor(e){
    this.setState((state, props) =>({
      backColor: e,
      backFooter: e
    }));
  }

  render(){
    return (
      <div>
          {/* <div className={this.state.backColor}> */}
          <div className="App">
            <div className="App-header">
              <Table parentMethod={this.handleColor}/> 
            </div>
            <div className={this.state.backColor}>
              <Footer />
            </div>
          </div>
          
      </div>
    )
  }
}

export default App;
  