import React from 'react';
import Document from 'react-ionicons/lib/MdDocument'
import PDF from '../pdf/yasser.pdf'

class Resume extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            color : "#FFFFFF",
            isHover: false,
            size : "4em"
        }
        this.colorChange = this.colorChange.bind(this);
    }
    
    colorChange(){
        let curColor;
        if(this.state.isHover){
            curColor = "#FFFFFF"
            this.setState((state,props) => ({
                color: curColor,
                isHover: false,
                shadowDrop: "",
                shadow: ""
            }));
        }else{
            
            curColor = '#FDB1FF'
            curColor = "#FFAC81"
            curColor= "#2dbcad";
            curColor="#f27f3d";

            this.setState((state,props) => ({
                color: curColor, 
                isHover: true,
                shadowDrop: "drop-shadow(0 0  2px #f27f3d)",
                shadow: "0 0  2px #f27f3d"
            }));
        }
        
    }
    
    render(){
        return(
            <div onMouseEnter={this.colorChange} onMouseLeave={this.colorChange}>
                <a href = {PDF} target="_blank">
                    <div style={{filter:this.state.shadowDrop}}>
                        <Document color= {this.state.color} fontSize={this.state.size}/>
                    </div>
                    <span style={{color: this.state.color, fontSize:"2em", fontWeight:"bolder", textShadow:this.state.shadow}}>Resume</span>
                </a>
            </div>
        )
    }
}
export default Resume;

