import React from 'react';
import { Github } from '@icons-pack/react-simple-icons';
// import '../App.css'


class GitHub extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            color : "#FFFFFF",
            isHover: false,
            size : "4em",
            shadow : "",
            shadowDrop : ""
        }
        this.colorChange = this.colorChange.bind(this);
        this.sizeChange = this.sizeChange.bind(this);
        // this.changeBack = this.changeBack.bind(this);
    }


    colorChange(){
        // this.changeBack();
        let curColor;
        if(this.state.isHover){
            // curColor = "#000000" 
            curColor = "#FFFFFF"
            this.setState((state,props) => ({
                color: curColor,
                isHover: false,
                shadowDrop: "",
                shadow: ""
            }));
        }else{
            //curColor = "#cd5c5c"
            curColor = "#181717"
            this.setState((state,props) => ({
                color: curColor,
                isHover: true,
                shadowDrop: "drop-shadow(0 0  2px #181717)",
                shadow: "0 0 2px #181717"
            }));
        }
        
    }



    sizeChange(){


    }
    

    
    render(){
        return(
            <div onMouseEnter={this.colorChange } onMouseLeave={this.colorChange} onMouseOver={this.changeBack}>
                <a href =  "https://github.com/yalnader" target="_blank">
                    <div style={{filter:this.state.shadowDrop}}>
                        <Github  size={this.state.size} color={this.state.color }  />
                    </div>
                    <span style={{color: this.state.color, fontSize:"2em", fontWeight:"bolder", textShadow:this.state.shadow}}>Github</span>
                </a>
            </div>
        )
    }
}
export default GitHub;





