import React from 'react';
import './Table.css'
import { ReactJs } from '@icons-pack/react-simple-icons';
import Github from './components/Github';
import LinkedIn from './components/Linkedin';
import Work  from './components/Work';
import Resume  from './components/Resume';
import { Fade } from 'react-animation-components'
import DownArrow from 'react-ionicons/lib/IosArrowDown'
import { goToAnchor } from 'react-scrollable-anchor'
import AnchorLink from 'react-anchor-link-smooth-scroll'

class Table extends React.Component{

    constructor(props){
        super(props);
        this.state ={
            work : false,
            backColor: 'App-footer'
        }
        this.githubColor = this.githubColor.bind(this)
        this.linkedinColor =this.linkedinColor.bind(this);
        this.workColor =this.workColor.bind(this);
        this.resumeColor =this.resumeColor.bind(this);

    }

    githubColor(){
        console.log("github Color is wokring");
        this.setState((state,props) => ({
            backColor: 'github'
        }));
        console.log(this.state.backColor);

    }
    linkedinColor(){
        console.log("linkedin Color is wokring");
        this.setState((state,props) => ({
            backColor: 'linkedin'
        }));
        console.log(this.state.backColor);

    }
    resumeColor(){
        console.log("resume Color is wokring");
        this.setState((state,props) => ({
            backColor: 'resume'
        }));
        console.log(this.state.backColor);

    }
    workColor(){
        console.log("work Color is wokring");
        this.setState((state,props) => ({
            backColor: 'work'
        }));
        console.log(this.state.backColor);

    }

    handleColor = ()=>{
        this.props.parentMethod(this.state.backColor);
    }
    // make a footer, with the graidents you had, it'll be a little easter egg
    render(){
       return( 
            <Fade in>
                <div className="topPart">
                    <h1>Hi there!</h1>
                    <p>I'm <span className="name">Yasser Alnader</span>, a second year Software Engineering student at the University of Ottawa</p>
                    {/* <p>I spend my free time doing a wide variety of things, from catching up on missed sleep, watching anime, <br></br> playing soccer, as well as working on a few of my side projects </p> */}
                    <p>For the Winter 2020 term I'm a Data Visualization intern at <span className="name">Kinaxis</span> </p>
                    <p>I also began my job search for a internship in <span className="name"> Fall 2020</span></p>
                  
                    <p></p>
                </div>
                <table onMouseOver ={this.handleColor}>
                    <thead>

                    </thead>

                    <tbody>
                            <tr className="topRow" >
                                <td onMouseEnter={this.githubColor}>
                                    <Github />
                                </td>

                                <td onMouseEnter={this.resumeColor}>
                                    <Resume/>
                                </td>

                                <td onMouseEnter={this.linkedinColor}>
                                    <LinkedIn/>
                                </td>

                            </tr>

                    </tbody>
                    <tfoot>
                        <tr>
                            <td>
                                <AnchorLink href="#contactInfo">    
                                    <div className="table-foot" >
                                        <div className="contact-title" >
                                            Contact
                                        </div>
                                        <div id="downArrow">
                                            <DownArrow fontSize="3em" color="#f27f3d" id="downArrow" />
                                        </div>
                                        
                                    </div>
                                </AnchorLink>
                            </td>
                        </tr>
                        
                    </tfoot>
                </table>
            </Fade>

       ) 
    }

}

export default Table;