import React from 'react';
import { Linkedin } from '@icons-pack/react-simple-icons';

class LinkedIn extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            color : "#FFFFFF",
            isHover: false,
            size : "4em",
            shadow : "",
            shadowDrop : ""
        }
        this.colorChange = this.colorChange.bind(this);
    }

    colorChange(){
        let curColor;
        if(this.state.isHover){
            // curColor = "#000000" 
            curColor = "#FFFFFF"
            this.setState((state,props) => ({
                color: curColor,
                isHover: false,
                shadowDrop: "",
                shadow: ""
            }));
        }else{
            //curColor = "#cd5c5c"
            curColor = "#0077B5"
            this.setState((state,props) => ({
                color: curColor,
                isHover: true,
                shadowDrop: "drop-shadow(0 0  2px #0077B5)",
                shadow: "0 0  2px #0077B5"
            }));
        }
        
    }
    render(){
        return(
            <div onMouseEnter={this.colorChange} onMouseLeave={this.colorChange}>
                <a href="https://www.linkedin.com/in/yalnader/" target="_blank">
                    <div style={{filter:this.state.shadowDrop}}>
                        <Linkedin id="linkedin"  size={this.state.size} color = {this.state.color}/>
                    </div>
                    <span style={{color: this.state.color, fontSize:"2em", fontWeight:"bolder", textShadow:this.state.shadow}}>LinkedIn</span>
                </a>
            </div>
        )
    }
}
export default LinkedIn;

